.shortenedLink {
    display: flex;
    width: 70%;
    border-radius: 8px;
    background-color: white;
    align-items: center;
    padding: 10px 20px;
    margin-top: 10px;
}

.shortenedLink > .shortLink {
    margin-left: auto;
    text-decoration: none;
    color: hsl(180, 66%, 49%);
}

.buttonPrimary {
    /* width: 15%; */
    text-decoration: none;
    font-size: 14px;
    color: white;
    padding: 10px 25px;
    background-color: hsl(180, 66%, 49%);
    border-radius: 8px;
    outline: none;
    margin-left: 15px;
    cursor: pointer;
    border: none;
}

.buttonPrimary:hover {
    background-color: hsl(180, 42%, 63%);
    transition: 0.5s background-color;
}

.shortenedLink > .shortLink:hover {
    color: hsl(180, 42%, 63%);
    transition: 0.5s color;
}

.shortenedLink__hr {
    display: none;
}

@media (max-width: 700px) {
    .shortenedLink {
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        text-align: center;
    }

    .shortenedLink__hr {
        display: block;
        border: 0;
        width: 100%;
        margin: 5px 0 !important;
        height: 1px;
        background-color: rgb(192, 190, 190);
    }

    /* .shortenedLink > p {
        border-bottom: 1px solid gray;
    } */

    .shortenedLink > * {
        margin: 12px 0;
    }

    .shortenedLink > .shortLink {
        margin-left: 0;
    }

    .buttonPrimary {
        margin: 0;
        width: 100%;
        text-align: center;
    }
}