.statCard {
    background-color: whitesmoke;
    min-height: 180px;
    margin: 15px;
    padding: 10px 30px 35px 30px;
    text-align: left;
    position: relative;
    display: flex;
    border-radius: 5px;
    flex-direction: column;
    justify-content: flex-end;
    max-width: 20%;
}

.statCard > .icon {
    background-color: hsl(257, 27%, 26%);
    height: 80px;
    width: 80px;
    border-radius: 99px;
    position: absolute;
    top: -40px;
    left: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.statCard > h2 {
    font-size: 20px;
    margin-bottom: 15px;
    margin-top: 40px;
}

.statCard > p {
    font-size: 13px;
    font-weight: 500;
    color: hsl(257, 7%, 63%);
}

@media (max-width: 960px) {
    .statCard {
        justify-content: center;
        align-items: center;
        text-align: center;
        max-width: 100%;
        width: 90%;
        margin-bottom: 50px;
    }
    .statCard > .icon {
        position: relative;
        left: 0;
        top: -55px;
        /* left: 45%; */
        /* left: 44.9%; */
    }

    .statCard > h2 {
        margin-top: -30px;
    }
}

@media (max-width: 940px) {
    .statCard > .icon {
        position: relative;
        /* left: 38%; */
        /* left: 44.9%; */
    }

}